@import "./globals.scss";

header {
  width: 100vw;
  height: 10vh;
  background-color: $primary-light-color;
  border-bottom: 1px solid $primary-color;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  .logo {
    margin: 0rem 2rem;
    img {
      height: 2.8rem;
    }
  }
  .header-navigation {
    text-decoration: none;
    margin: 0 2rem;
    li {
      display: inline-block;
      margin: 0 0.8rem;
      border-bottom: 2px solid transparent;
      a {
        font-style: italic;
        font-size: 1.4rem;
        color: $primary-color;
        font-weight: 200;
      }
      a:hover {
        border-bottom: 2px solid transparent;
      }
    }
    li:hover {
      border-bottom: 2px solid $primary-color;
    }
  }
}
